import { useEffect, useState } from "react";

export type ChallengeState = "open" | "closed" | "hidden";

export interface Challenge {
  title?: string;
  description?: string;
  maxPoints: number;
  reachedPoints: number;
  minClosed: number;
  state: ChallengeState;
}

export interface Data {
  gameId: number;
  gameTitle: string;
  totalReachedPoints: number;
  totalMaxPoints: number;
  challenges: Challenge[];
}

async function getData(gameId: string): Promise<Data> {
  const res = await fetch(`/api/game/${gameId}`);
  if (res.status === 200) return res.json();
  console.error(`Fetch error: ${res.status} ${res.statusText} ${res.text()}`);
  throw new Error(res.statusText);
}

export function useData(gameId?: string): { data?: Data; error?: any } {
  const [data, setData] = useState<Data | undefined>(undefined);
  const [error, setError] = useState<any | undefined>(undefined);

  useEffect(() => {
    if (gameId == null) {
      setData(undefined);
      setError(new Error("GameId Not Given"));
    } else {
      (async () => {
        try {
          setData(await getData(gameId));
          setError(undefined);
        } catch (err) {
          setData(undefined);
          setError(err);
        }
      })();
    }
  }, [gameId]);

  return { data, error };
}
